body {
  margin: 0;
  padding: 0;
  font-family:
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    "Roboto",
    "Oxygen"
    "Ubuntu",
    "Cantarell",
    "Fira Sans",
    "Droid Sans",
    "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 12px;
}

code {
  font-family:
    source-code-pro,
    Menlo,
    Monaco,
    Consolas,
    "Courier New",
    monospace;
}

.App {
  text-align: left;
  background-color: #f0eded;
}

.App-header,
.sidebar,
.mainbody {
  background-color: #fff;
  margin: 15px;
  padding: 15px;
}

h1,
h3 {
  text-align: center;
}

h3 {
  color: #555;
  font-weight: normal;
}

main {
  display: grid;
  grid-template:
    [row1] "sidebar" "mainbody" [row1]
    / 0.75fr 1fr;
}

.App-header {
  margin: 0;
}

.sidebar {
  grid-area: "sidebar";
}

.mainbody {
  grid-area: "mainbody";
}

.sidebar,
.mainbody {
  float: left;
}

img {
  width: 100%;
  height: auto;
}

.product-img {
  text-align: center;
}

.product-img img {
  max-width: 150px;
}

.sidebar .tags {
  display: -webkit-flex;
  display: flex;
  -webkit-flex-wrap: wrap;
          flex-wrap: wrap;
}

.sidebar .tags .tag {
  display: -webkit-inline-flex;
  display: inline-flex;
  border: 1px solid #e3e3e3;
  border-radius: 2px;
  padding: 5px 15px;
  margin: 10px;
  line-height: 12px;
}

table {
  width: 100%;
}

